import React, { useState, useEffect } from 'react';
import { ethers } from 'ethers';
// TODO: Replace with actual ABI and contract address
import DiagnosticContractABI from '../abis/DiagnosticContract.json';
import contractAddresses from '../config/contractAddresses.json';

const initialState = {
  wallet: '',
  name: '',
  location: '',
  contactNumber: '',
  email: '',
  hNumber: '',
  licenseNumber: '',
  establishedDate: '',
  servicesOffered: [],
  password: '',
  confirmPassword: '',
};

const services = [
  'X-Ray', 'MRI', 'CT Scan', 'Blood Test', 'Ultrasound', 'ECG', 'Other'
];

const DiagnosticRegistrationForm = () => {
  const [form, setForm] = useState(initialState);
  const [errors, setErrors] = useState({});
  const [metaMaskConnected, setMetaMaskConnected] = useState(false);
  const [txStatus, setTxStatus] = useState('');
  const [txHash, setTxHash] = useState('');

  useEffect(() => {
    if (window.ethereum) {
      window.ethereum.request({ method: 'eth_accounts' }).then(accounts => {
        if (accounts.length > 0) {
          setForm(f => ({ ...f, wallet: accounts[0] }));
          setMetaMaskConnected(true);
        }
      });
    }
  }, []);

  useEffect(() => {
    validateForm();
    // eslint-disable-next-line
  }, [form]);

  const validateForm = () => {
    const newErrors = {};
    if (!form.wallet) newErrors.wallet = 'Connect MetaMask to autofill wallet address.';
    if (!form.name) newErrors.name = 'Diagnostic center name is required.';
    if (!form.location) newErrors.location = 'Location/Address is required.';
    if (!form.contactNumber) newErrors.contactNumber = 'Contact number is required.';
    else if (!/^\d{10}$/.test(form.contactNumber)) newErrors.contactNumber = 'Contact number must be 10 digits.';
    if (!form.email) newErrors.email = 'Email is required.';
    else if (!/^\S+@\S+\.\S+$/.test(form.email)) newErrors.email = 'Invalid email format.';
    if (!form.hNumber) newErrors.hNumber = 'H Number is required.';
    else if (!/^\d{6}$/.test(form.hNumber)) newErrors.hNumber = 'H Number must be exactly 6 digits.';
    if (!form.licenseNumber) newErrors.licenseNumber = 'License number is required.';
    if (!form.establishedDate) newErrors.establishedDate = 'Established date is required.';
    else if (new Date(form.establishedDate) > new Date()) newErrors.establishedDate = 'Established date cannot be in the future.';
    if (!form.servicesOffered.length) newErrors.servicesOffered = 'Select at least one service.';
    if (!form.password) newErrors.password = 'Password is required.';
    else if (form.password.length < 8) newErrors.password = 'Password must be at least 8 characters.';
    if (form.password !== form.confirmPassword) newErrors.confirmPassword = 'Passwords do not match.';
    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleChange = e => {
    const { name, value, type, checked } = e.target;
    if (type === 'checkbox') {
      setForm(f => {
        const updated = checked
          ? [...f.servicesOffered, value]
          : f.servicesOffered.filter(s => s !== value);
        return { ...f, servicesOffered: updated };
      });
    } else {
      setForm(f => ({ ...f, [name]: value }));
    }
  };

  const handleSubmit = async e => {
    e.preventDefault();
    if (!validateForm()) return;
    setTxStatus('');
    setTxHash('');
    try {
      if (!window.ethereum) throw new Error('MetaMask not detected');
      setTxStatus('Waiting for wallet confirmation...');
      const provider = new ethers.providers.Web3Provider(window.ethereum);
      const signer = provider.getSigner();
      const contract = new ethers.Contract(
        contractAddresses.DiagnosticContract,
        DiagnosticContractABI.abi,
        signer
      );
      const tx = await contract.registerDiagnosticCenter(
        form.hNumber,
        form.name,
        form.location,
        form.contactNumber,
        form.email,
        form.licenseNumber,
        form.establishedDate,
        form.servicesOffered,
        ethers.utils.keccak256(ethers.utils.toUtf8Bytes(form.password))
      );
      setTxStatus('Transaction sent. Waiting for confirmation...');
      setTxHash(tx.hash);
      await tx.wait();
      setTxStatus('Registration successful!');
    } catch (err) {
      setTxStatus('Registration failed: ' + (err.reason || err.message));
    }
  };

  return (
    <div className="container mt-5">
      <h2 className="mb-4 text-center">Diagnostic Center Registration</h2>
      <form className="card p-4 shadow-sm mx-auto" style={{ maxWidth: 700 }} onSubmit={handleSubmit} autoComplete="off">
        <div className="mb-3">
          <label className="form-label">Wallet Public Address</label>
          <input type="text" className={`form-control ${errors.wallet ? 'is-invalid' : ''}`} name="wallet" value={form.wallet} disabled />
          {errors.wallet && <div className="invalid-feedback">{errors.wallet}</div>}
          {!metaMaskConnected && <div className="form-text text-danger">MetaMask not connected</div>}
        </div>
        <div className="mb-3">
          <label className="form-label">Diagnostic Center Name</label>
          <input type="text" className={`form-control ${errors.name ? 'is-invalid' : ''}`} name="name" value={form.name} onChange={handleChange} />
          {errors.name && <div className="invalid-feedback">{errors.name}</div>}
        </div>
        <div className="mb-3">
          <label className="form-label">Location/Address</label>
          <textarea className={`form-control ${errors.location ? 'is-invalid' : ''}`} name="location" value={form.location} onChange={handleChange} />
          {errors.location && <div className="invalid-feedback">{errors.location}</div>}
        </div>
        <div className="mb-3">
          <label className="form-label">Contact Number</label>
          <input type="text" className={`form-control ${errors.contactNumber ? 'is-invalid' : ''}`} name="contactNumber" value={form.contactNumber} onChange={handleChange} maxLength={10} />
          {errors.contactNumber && <div className="invalid-feedback">{errors.contactNumber}</div>}
        </div>
        <div className="mb-3">
          <label className="form-label">Email ID</label>
          <input type="email" className={`form-control ${errors.email ? 'is-invalid' : ''}`} name="email" value={form.email} onChange={handleChange} />
          {errors.email && <div className="invalid-feedback">{errors.email}</div>}
        </div>
        <div className="mb-3">
          <label className="form-label">H Number</label>
          <input type="text" className={`form-control ${errors.hNumber ? 'is-invalid' : ''}`} name="hNumber" value={form.hNumber} onChange={handleChange} maxLength={6} />
          {errors.hNumber && <div className="invalid-feedback">{errors.hNumber}</div>}
        </div>
        <div className="mb-3">
          <label className="form-label">License Number</label>
          <input type="text" className={`form-control ${errors.licenseNumber ? 'is-invalid' : ''}`} name="licenseNumber" value={form.licenseNumber} onChange={handleChange} />
          {errors.licenseNumber && <div className="invalid-feedback">{errors.licenseNumber}</div>}
        </div>
        <div className="mb-3">
          <label className="form-label">Established Date</label>
          <input type="date" className={`form-control ${errors.establishedDate ? 'is-invalid' : ''}`} name="establishedDate" value={form.establishedDate} onChange={handleChange} />
          {errors.establishedDate && <div className="invalid-feedback">{errors.establishedDate}</div>}
        </div>
        <div className="mb-3">
          <label className="form-label">Services Offered</label>
          <div className="d-flex flex-wrap gap-2">
            {services.map(service => (
              <div className="form-check" key={service}>
                <input
                  className="form-check-input"
                  type="checkbox"
                  name="servicesOffered"
                  value={service}
                  id={service}
                  checked={form.servicesOffered.includes(service)}
                  onChange={handleChange}
                />
                <label className="form-check-label" htmlFor={service}>{service}</label>
              </div>
            ))}
          </div>
          {errors.servicesOffered && <div className="text-danger small mt-1">{errors.servicesOffered}</div>}
        </div>
        <div className="mb-3">
          <label className="form-label">Password</label>
          <input type="password" className={`form-control ${errors.password ? 'is-invalid' : ''}`} name="password" value={form.password} onChange={handleChange} />
          {errors.password && <div className="invalid-feedback">{errors.password}</div>}
        </div>
        <div className="mb-3">
          <label className="form-label">Confirm Password</label>
          <input type="password" className={`form-control ${errors.confirmPassword ? 'is-invalid' : ''}`} name="confirmPassword" value={form.confirmPassword} onChange={handleChange} />
          {errors.confirmPassword && <div className="invalid-feedback">{errors.confirmPassword}</div>}
        </div>
        <button type="submit" className="btn btn-primary w-100" disabled={Object.keys(errors).length > 0}>Register</button>
        {txStatus && <div className="alert mt-3 mb-0 alert-info">{txStatus} {txHash && (<span>Tx: <a href={`https://sepolia.etherscan.io/tx/${txHash}`} target="_blank" rel="noopener noreferrer">{txHash.slice(0, 10)}...</a></span>)}</div>}
      </form>
    </div>
  );
};

export default DiagnosticRegistrationForm; 