import React, { useState, useEffect } from 'react';
import { ethers } from 'ethers';
import PatientContractABI from '../abis/PatientContract.json';
import contractAddresses from '../config/contractAddresses.json';
import { uploadFile } from '../utils/uploadFile';
import DoctorContractABI from '../abis/DoctorContract.json';
import EHRSystemABI from '../abis/EHRSystem.json';
import DiagnosticContractABI from '../abis/DiagnosticContract.json';
import Modal from 'react-bootstrap/Modal';
import Button from 'react-bootstrap/Button';

const PatientDashboard = () => {
  const [profile, setProfile] = useState(null);
  const [records, setRecords] = useState([]);
  const [permissions, setPermissions] = useState([]);
  const [loading, setLoading] = useState(true);
  const [editMode, setEditMode] = useState(false);
  const [form, setForm] = useState({});
  const [ipfsFile, setIpfsFile] = useState(null);
  const [recordType, setRecordType] = useState('');
  const [txStatus, setTxStatus] = useState('');
  const [provider, setProvider] = useState('pinata');
  const [doctorList, setDoctorList] = useState([]);
  const [selectedDoctor, setSelectedDoctor] = useState('');
  const [consultancyReports, setConsultancyReports] = useState([]);
  const [diagnosticReports, setDiagnosticReports] = useState([]);
  const [showUploadModal, setShowUploadModal] = useState(false);
  const [doctorDetailsMap, setDoctorDetailsMap] = useState({});

  const user = JSON.parse(localStorage.getItem('ehr_user'));

  useEffect(() => {
    if (!user || user.role !== 'patient') {
      window.location.href = '/login/patient';
      return;
    }
    window.ethereum.request({ method: 'eth_accounts' }).then(accounts => {
      if (accounts[0].toLowerCase() !== user.wallet.toLowerCase()) {
        setTxStatus('Connected wallet does not match patient account. Please switch MetaMask account.');
      } else {
        loadData();
        loadDoctors();
      }
    });
    // eslint-disable-next-line
  }, []);

  useEffect(() => {
    if (!user || user.role !== 'patient') return;
    if (!permissions.length) return;
    fetchConsultancyReports();
    fetchDiagnosticReports();
    // eslint-disable-next-line
  }, [permissions]);

  const loadData = async () => {
    setLoading(true);
    try {
      const provider = new ethers.providers.Web3Provider(window.ethereum);
      const signer = provider.getSigner();
      const contractWithSigner = new ethers.Contract(contractAddresses.PatientContract, PatientContractABI.abi, signer);
      const p = await contractWithSigner.patients(user.wallet);
      setProfile(p);
      setForm({
        name: p.name,
        bloodGroup: p.bloodGroup,
        homeAddress: p.homeAddress,
        dateOfBirth: p.dateOfBirth,
        gender: p.gender,
        email: p.email
      });
      const recs = await contractWithSigner.getMedicalRecords(user.wallet);
      setRecords(recs);
      const perms = await contractWithSigner.getPermissions(user.wallet);
      setPermissions(perms);
    } catch (err) {
      if (err.message && err.message.includes('Not authorized')) {
        setTxStatus('You are not authorized to view these records.');
      } else {
        setTxStatus('Error loading data: ' + err.message);
      }
    }
    setLoading(false);
  };

  const handleEdit = () => setEditMode(true);
  const handleCancel = () => setEditMode(false);
  const handleChange = e => setForm(f => ({ ...f, [e.target.name]: e.target.value }));

  const handleSave = async () => {
    setTxStatus('');
    try {
      const provider = new ethers.providers.Web3Provider(window.ethereum);
      const signer = provider.getSigner();
      const contract = new ethers.Contract(contractAddresses.PatientContract, PatientContractABI.abi, signer);
      const tx = await contract.updateProfile(
        form.name,
        form.bloodGroup,
        form.homeAddress,
        form.dateOfBirth,
        form.gender,
        form.email
      );
      setTxStatus('Profile update sent. Waiting for confirmation...');
      await tx.wait();
      setTxStatus('Profile updated!');
      setEditMode(false);
      loadData();
    } catch (err) {
      setTxStatus('Profile update failed: ' + err.message);
    }
  };

  const handleFileChange = e => setIpfsFile(e.target.files[0]);

  const handleUpload = async () => {
    setTxStatus('');
    if (!ipfsFile || !recordType) {
      setTxStatus('Select a file and record type.');
      return;
    }
    try {
      setTxStatus(`Uploading file to ${provider === 'pinata' ? 'Pinata' : 'IPFS'}...`);
      const hash = await uploadFile(ipfsFile, provider);
      setTxStatus(`File uploaded to ${provider === 'pinata' ? 'Pinata' : 'IPFS'}. Storing on blockchain...`);
      const providerEth = new ethers.providers.Web3Provider(window.ethereum);
      const signer = providerEth.getSigner();
      const contract = new ethers.Contract(contractAddresses.PatientContract, PatientContractABI.abi, signer);
      const tx = await contract.uploadMedicalRecord(hash, recordType);
      setTxStatus('Record upload sent. Waiting for confirmation...');
      await tx.wait();
      setTxStatus('Record uploaded!');
      setIpfsFile(null);
      setRecordType('');
      loadData();
    } catch (err) {
      setTxStatus('Record upload failed: ' + err.message);
    }
  };

  const handleGrant = async () => {
    setTxStatus('');
    if (!selectedDoctor) {
      setTxStatus('Select a doctor.');
      return;
    }
    try {
      const provider = new ethers.providers.Web3Provider(window.ethereum);
      const signer = provider.getSigner();
      const contract = new ethers.Contract(contractAddresses.PatientContract, PatientContractABI.abi, signer);
      const tx = await contract.grantPermission(selectedDoctor);
      setTxStatus('Permission grant sent. Waiting for confirmation...');
      await tx.wait();
      setTxStatus('Permission granted!');
      setSelectedDoctor('');
      loadData();
    } catch (err) {
      setTxStatus('Permission grant failed: ' + err.message);
    }
  };

  const handleRevoke = async doctorAddr => {
    setTxStatus('');
    try {
      const provider = new ethers.providers.Web3Provider(window.ethereum);
      const signer = provider.getSigner();
      const contract = new ethers.Contract(contractAddresses.PatientContract, PatientContractABI.abi, signer);
      const tx = await contract.revokePermission(doctorAddr);
      setTxStatus('Permission revoke sent. Waiting for confirmation...');
      await tx.wait();
      setTxStatus('Permission revoked!');
      loadData();
    } catch (err) {
      setTxStatus('Permission revoke failed: ' + err.message);
    }
  };

  const loadDoctors = async () => {
    try {
      const provider = new ethers.providers.Web3Provider(window.ethereum);
      const signer = provider.getSigner();
      const ehrSystem = new ethers.Contract(contractAddresses.EHRSystem, EHRSystemABI.abi, signer);
      const doctorContract = new ethers.Contract(contractAddresses.DoctorContract, DoctorContractABI.abi, signer);
      const doctorAddresses = await ehrSystem.getAllDoctors();
      const doctorDetails = await Promise.all(
        doctorAddresses.map(async (addr) => {
          const doc = await doctorContract.doctors(addr);
          if (doc.exists) {
            return {
              address: addr,
              name: doc.name,
              specialization: doc.specialization,
              hNumber: doc.hNumber
            };
          }
          return null;
        })
      );
      const detailsMap = {};
      doctorDetails.filter(Boolean).forEach(doc => {
        detailsMap[doc.address.toLowerCase()] = doc;
      });
      setDoctorDetailsMap(detailsMap);
      setDoctorList(doctorDetails.filter(Boolean));
    } catch (err) {
      // Optionally handle error
    }
  };

  const fetchConsultancyReports = async () => {
    try {
      const provider = new ethers.providers.Web3Provider(window.ethereum);
      const signer = provider.getSigner();
      const doctorContract = new ethers.Contract(contractAddresses.DoctorContract, DoctorContractABI.abi, signer);
      const ehrSystem = new ethers.Contract(contractAddresses.EHRSystem, EHRSystemABI.abi, signer);
      
      // Get ALL doctors (not just active permissions) to check for any reports they may have created
      const allDoctors = await ehrSystem.getAllDoctors();
      
      const doctorDetails = await Promise.all(
        allDoctors.map(async (addr) => {
          try {
            const doc = await doctorContract.doctors(addr);
            return doc.exists ? { address: addr, name: doc.name, specialization: doc.specialization } : null;
          } catch {
            return null;
          }
        })
      );
      
      const reportsByDoctor = await Promise.all(
        allDoctors.map(async (addr, idx) => {
          try {
            // Try to get reports from each doctor for this patient
            // The contract function will only return reports if the doctor has/had access
            const reports = await doctorContract.getConsultancyReportsForPatientByDoctor(addr, user.wallet);
            return {
              doctor: doctorDetails[idx],
              reports: reports
            };
          } catch (err) {
            // If doctor doesn't have access, just return empty reports
            return { doctor: doctorDetails[idx], reports: [] };
          }
        })
      );
      
      setConsultancyReports(reportsByDoctor.filter(r => r.doctor && r.reports.length > 0));
    } catch (err) {
      console.log('Error fetching consultancy reports:', err.message);
    }
  };

  const fetchDiagnosticReports = async () => {
    try {
      const provider = new ethers.providers.Web3Provider(window.ethereum);
      const signer = provider.getSigner();
      const diagnosticContract = new ethers.Contract(contractAddresses.DiagnosticContract, DiagnosticContractABI.abi, signer);
      
      // Get diagnostic reports for this patient
      const reports = await diagnosticContract.getReportsForUser(user.wallet);
      
      // Get diagnostic center names for each report
      const reportsWithCenterNames = await Promise.all(
        reports.map(async (report) => {
          try {
            // Get the diagnostic center address from the report
            const diagnosticCenterAddress = await diagnosticContract.getDiagnosticCenterFromReport(report.reportId);
            const centerName = await diagnosticContract.getDiagnosticCenterName(diagnosticCenterAddress);
            return { ...report, centerName };
          } catch {
            return { ...report, centerName: 'Unknown Center' };
          }
        })
      );
      
      setDiagnosticReports(reportsWithCenterNames);
    } catch (err) {
      console.log('Error fetching diagnostic reports:', err.message);
    }
  };

  if (loading) return <div className="container mt-5 text-center">Loading...</div>;

  return (
    <div className="container mt-5">
      <h2 className="mb-4">Patient Dashboard</h2>
      {txStatus && <div className="alert alert-info">{txStatus}</div>}
      <div className="row">
        <div className="col-md-6">
          <div className="card mb-4">
            <div className="card-header d-flex justify-content-between align-items-center">
              <span>Profile</span>
              {!editMode && <button className="btn btn-sm btn-outline-primary" onClick={handleEdit}>Edit</button>}
            </div>
            <div className="card-body">
              {editMode ? (
                <>
                  <input className="form-control mb-2" name="name" value={form.name} onChange={handleChange} placeholder="Name" />
                  <input className="form-control mb-2" name="bloodGroup" value={form.bloodGroup} onChange={handleChange} placeholder="Blood Group" />
                  <input className="form-control mb-2" name="homeAddress" value={form.homeAddress} onChange={handleChange} placeholder="Home Address" />
                  <input className="form-control mb-2" name="dateOfBirth" value={form.dateOfBirth} onChange={handleChange} placeholder="Date of Birth" />
                  <input className="form-control mb-2" name="gender" value={form.gender} onChange={handleChange} placeholder="Gender" />
                  <input className="form-control mb-2" name="email" value={form.email} onChange={handleChange} placeholder="Email" />
                  <button className="btn btn-success me-2" onClick={handleSave}>Save</button>
                  <button className="btn btn-secondary" onClick={handleCancel}>Cancel</button>
                </>
              ) : profile && (
                <>
                  <div><b>Name:</b> {profile.name}</div>
                  <div><b>Blood Group:</b> {profile.bloodGroup}</div>
                  <div><b>Home Address:</b> {profile.homeAddress}</div>
                  <div><b>Date of Birth:</b> {profile.dateOfBirth}</div>
                  <div><b>Gender:</b> {profile.gender}</div>
                  <div><b>Email:</b> {profile.email}</div>
                </>
              )}
            </div>
          </div>
          <div className="card mb-4">
            <div className="card-header">Medical Records</div>
            <div className="card-body">
              <Button variant="primary" onClick={() => setShowUploadModal(true)} className="mb-3">Upload File</Button>
              <ul className="list-group">
                {records.map((rec, i) => (
                  <li className="list-group-item" key={i}>
                    <div><b>Type:</b> {rec.recordType}</div>
                    <div><b>Date:</b> {rec.timestamp && new Date(Number(rec.timestamp.toString()) * 1000).toLocaleString()}</div>
                    <div>
                      <b>IPFS:</b> 
                      <button className="btn btn-outline-primary btn-sm ms-2" onClick={() => window.open(`https://ipfs.io/ipfs/${rec.ipfsHash}`, '_blank', 'noopener,noreferrer')}>View Record</button>
                    </div>
                  </li>
                ))}
              </ul>
              <Modal show={showUploadModal} onHide={() => setShowUploadModal(false)}>
                <Modal.Header closeButton>
                  <Modal.Title>Upload Medical Record</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                  <div className="alert alert-secondary p-2 mb-2" style={{fontSize: '0.95em'}}>
                    Accepted file types: <b>PDF, JPG, PNG, DOCX</b>
                  </div>
                  <input type="file" className="form-control mb-2" onChange={handleFileChange} accept=".pdf,.jpg,.jpeg,.png,.docx,application/pdf,image/jpeg,image/png,application/vnd.openxmlformats-officedocument.wordprocessingml.document" />
                  <input type="text" className="form-control mb-2" placeholder="Record Type (e.g., Lab Report)" value={recordType} onChange={e => setRecordType(e.target.value)} />
                  <select className="form-select mb-2" value={provider} onChange={e => setProvider(e.target.value)}>
                    <option value="pinata">Pinata</option>
                    <option value="ipfs">IPFS (Infura/Local)</option>
                  </select>
                </Modal.Body>
                <Modal.Footer>
                  <Button variant="secondary" onClick={() => setShowUploadModal(false)}>Cancel</Button>
                  <Button variant="primary" onClick={async () => { await handleUpload(); setShowUploadModal(false); }}>Upload</Button>
                </Modal.Footer>
              </Modal>
            </div>
          </div>
        </div>
        <div className="col-md-6">
          <div className="card mb-4">
            <div className="card-header">Permission Management</div>
            <div className="card-body">
              <div className="mb-3 d-flex">
                <select className="form-select me-2" value={selectedDoctor} onChange={e => setSelectedDoctor(e.target.value)}>
                  <option value="">Select Doctor</option>
                  {doctorList.map(doc => (
                    <option key={doc.address} value={doc.address}>
                      {doc.name} ({doc.specialization})
                    </option>
                  ))}
                </select>
                <button className="btn btn-success" onClick={handleGrant}>Grant</button>
              </div>
              <ul className="list-group">
                {permissions.map((perm, i) => {
                  const doc = doctorDetailsMap[perm.doctor.toLowerCase()];
                  return (
                    <li className="list-group-item d-flex justify-content-between align-items-center" key={i}>
                      <span>
                        <b>Doctor:</b> {doc ? `${doc.name} (${doc.specialization})` : perm.doctor}
                        {doc && <>
                          <br/><b>H Number:</b> {doc.hNumber}
                          <br/><b>Wallet:</b> {doc.address}
                        </>}
                        <br/><b>Status:</b> {perm.active ? 'Active' : 'Revoked'}
                      </span>
                      {perm.active && <button className="btn btn-danger btn-sm" onClick={() => handleRevoke(perm.doctor)}>Revoke</button>}
                    </li>
                  );
                })}
              </ul>
            </div>
          </div>
          <div className="card mb-4">
            <div className="card-header">Consultancy Reports</div>
            <div className="card-body">
              {consultancyReports.length === 0 ? (
                <div>No consultancy reports found.</div>
              ) : (
                consultancyReports.map((entry, i) => {
                  // Check if this doctor has active permission
                  const hasActivePermission = permissions.some(p => 
                    p.doctor.toLowerCase() === entry.doctor.address.toLowerCase() && p.active
                  );
                  
                  return (
                    <div key={i} className="mb-3">
                      <div className="d-flex justify-content-between align-items-center">
                        <div><b>Doctor:</b> {entry.doctor.name} ({entry.doctor.specialization})</div>
                        {!hasActivePermission && (
                          <span className="badge bg-warning text-dark">Historical Access</span>
                        )}
                      </div>
                      <ul className="list-group">
                        {entry.reports.map((rep, j) => (
                          <li className="list-group-item" key={j}>
                            <div><b>Diagnosis:</b> {rep.diagnosis}</div>
                            <div><b>Prescription:</b> {rep.prescription}</div>
                            <div><b>Date:</b> {rep.timestamp && new Date(Number(rep.timestamp.toString()) * 1000).toLocaleString()}</div>
                          </li>
                        ))}
                      </ul>
                    </div>
                  );
                })
              )}
            </div>
          </div>
          <div className="card mb-4">
            <div className="card-header">Diagnostic Reports</div>
            <div className="card-body">
              {diagnosticReports.length === 0 ? (
                <div>No diagnostic reports found.</div>
              ) : (
                <ul className="list-group">
                  {diagnosticReports.map((rep, i) => (
                    <li className="list-group-item" key={i}>
                      <div><b>Test Type:</b> {rep.testType}</div>
                      <div><b>Results:</b> {rep.results}</div>
                      <div><b>Date:</b> {rep.timestamp && new Date(Number(rep.timestamp.toString()) * 1000).toLocaleString()}</div>
                      <div><b>Diagnostic Center:</b> {rep.centerName || 'Unknown Center'}</div>
                      <div>
                        <b>IPFS:</b> <button className="btn btn-outline-primary btn-sm ms-2" onClick={() => window.open(`https://ipfs.io/ipfs/${rep.ipfsHash}`, '_blank', 'noopener,noreferrer')}>View Record</button>
                      </div>
                    </li>
                  ))}
                </ul>
              )}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default PatientDashboard; 