import React, { useState, useEffect } from 'react';
import { ethers } from 'ethers';
import DoctorContractABI from '../abis/DoctorContract.json';
import contractAddresses from '../config/contractAddresses.json';
import PatientContractABI from '../abis/PatientContract.json';
import EHRSystemABI from '../abis/EHRSystem.json';
import DiagnosticContractABI from '../abis/DiagnosticContract.json';
import Button from 'react-bootstrap/Button';

const DoctorDashboard = () => {
  const [profile, setProfile] = useState(null);
  const [editMode, setEditMode] = useState(false);
  const [form, setForm] = useState({});
  const [patients, setPatients] = useState([]);
  const [reports, setReports] = useState([]);
  const [selectedPatient, setSelectedPatient] = useState('');
  const [diagnosis, setDiagnosis] = useState('');
  const [prescription, setPrescription] = useState('');
  const [txStatus, setTxStatus] = useState('');
  const [loading, setLoading] = useState(true);
  const [patientRecords, setPatientRecords] = useState([]);
  const [recordStatus, setRecordStatus] = useState('');
  const [patientDiagnosticReports, setPatientDiagnosticReports] = useState([]);
  const [patientDetailsMap, setPatientDetailsMap] = useState({});

  const user = JSON.parse(localStorage.getItem('ehr_user'));

  useEffect(() => {
    if (!user || user.role !== 'doctor') {
      window.location.href = '/login/doctor';
      return;
    }
    loadData();
    // eslint-disable-next-line
  }, []);

  const loadData = async () => {
    setLoading(true);
    setTxStatus('');
    try {
      const provider = new ethers.providers.Web3Provider(window.ethereum);
      const signer = provider.getSigner();
      const contract = new ethers.Contract(contractAddresses.DoctorContract, DoctorContractABI.abi, signer);
      const patientContract = new ethers.Contract(contractAddresses.PatientContract, PatientContractABI.abi, signer);
      const ehrSystem = new ethers.Contract(contractAddresses.EHRSystem, EHRSystemABI.abi, signer);
      const d = await contract.doctors(user.wallet);
      setProfile(d);
      setForm({
        name: d.name,
        hospital: d.hospital,
        specialization: d.specialization,
        department: d.department,
        designation: d.designation,
        experience: d.experience && d.experience.toString(),
        email: d.email
      });
      // Fetch permitted patients from EHRSystem (on-chain)
      const permittedPatients = await ehrSystem.getPermittedPatientsForDoctor(user.wallet);
      // Fetch patient details for each address
      const detailsMap = {};
      await Promise.all(permittedPatients.map(async addr => {
        try {
          const p = await patientContract.patients(addr);
          detailsMap[addr.toLowerCase()] = {
            name: p.name,
            hNumber: p.hNumber,
            wallet: addr
          };
        } catch {}
      }));
      setPatientDetailsMap(detailsMap);
      setPatients(permittedPatients.map(addr => ({ patient: addr, active: true })));
      const reps = await contract.getConsultancyReports(user.wallet);
      setReports(reps);
    } catch (err) {
      setTxStatus('Error loading data: ' + err.message);
    }
    setLoading(false);
  };

  const handleEdit = () => setEditMode(true);
  const handleCancel = () => setEditMode(false);
  const handleChange = e => setForm(f => ({ ...f, [e.target.name]: e.target.value }));

  const handleSave = async () => {
    setTxStatus('');
    try {
      const provider = new ethers.providers.Web3Provider(window.ethereum);
      const signer = provider.getSigner();
      const contract = new ethers.Contract(contractAddresses.DoctorContract, DoctorContractABI.abi, signer);
      const tx = await contract.updateProfile(
        form.name,
        form.hospital,
        form.specialization,
        form.department,
        form.designation,
        Number(form.experience),
        form.email
      );
      setTxStatus('Profile update sent. Waiting for confirmation...');
      await tx.wait();
      setTxStatus('Profile updated!');
      setEditMode(false);
      loadData();
    } catch (err) {
      setTxStatus('Profile update failed: ' + err.message);
    }
  };

  const handleReport = async () => {
    setTxStatus('');
    if (!selectedPatient || !diagnosis || !prescription) {
      setTxStatus('Select patient and fill all fields.');
      return;
    }
    try {
      const provider = new ethers.providers.Web3Provider(window.ethereum);
      const signer = provider.getSigner();
      const contract = new ethers.Contract(contractAddresses.DoctorContract, DoctorContractABI.abi, signer);
      const tx = await contract.createConsultancyReport(selectedPatient, diagnosis, prescription);
      setTxStatus('Report creation sent. Waiting for confirmation...');
      await tx.wait();
      setTxStatus('Consultancy report created!');
      setDiagnosis('');
      setPrescription('');
      setSelectedPatient('');
      loadData();
    } catch (err) {
      setTxStatus('Report creation failed: ' + err.message);
    }
  };

  const handleViewRecords = async () => {
    setRecordStatus('');
    if (!selectedPatient) {
      setRecordStatus('Select a patient.');
      return;
    }
    try {
      const provider = new ethers.providers.Web3Provider(window.ethereum);
      const signer = provider.getSigner();
      const contract = new ethers.Contract(contractAddresses.PatientContract, PatientContractABI.abi, signer);
      const diagnosticContract = new ethers.Contract(contractAddresses.DiagnosticContract, DiagnosticContractABI.abi, signer);
      
      // Get medical records
      const recs = await contract.getMedicalRecords(selectedPatient);
      setPatientRecords(recs);
      
      // Get diagnostic reports for the selected patient
      const diagnosticReports = await diagnosticContract.getReportsForUser(selectedPatient);
      
      // Get diagnostic center names for each report
      const reportsWithCenterNames = await Promise.all(
        diagnosticReports.map(async (report) => {
          try {
            const diagnosticCenterAddress = await diagnosticContract.getDiagnosticCenterFromReport(report.reportId);
            const centerName = await diagnosticContract.getDiagnosticCenterName(diagnosticCenterAddress);
            return { ...report, centerName };
          } catch {
            return { ...report, centerName: 'Unknown Center' };
          }
        })
      );
      
      setPatientDiagnosticReports(reportsWithCenterNames);
    } catch (err) {
      if (err.message && err.message.includes('Not authorized')) {
        setRecordStatus('You are not authorized to view this patient\'s records.');
      } else {
        setRecordStatus('Error loading records: ' + err.message);
      }
      setPatientRecords([]);
      setPatientDiagnosticReports([]);
    }
  };

  if (loading) return <div className="container mt-5 text-center">Loading...</div>;

  return (
    <div className="container mt-5">
      <h2 className="mb-4">Doctor Dashboard</h2>
      {txStatus && <div className="alert alert-info">{txStatus}</div>}
      <div className="row">
        <div className="col-md-6">
          <div className="card mb-4">
            <div className="card-header d-flex justify-content-between align-items-center">
              <span>Profile</span>
              {!editMode && <button className="btn btn-sm btn-outline-primary" onClick={handleEdit}>Edit</button>}
            </div>
            <div className="card-body">
              {editMode ? (
                <>
                  <input className="form-control mb-2" name="name" value={form.name} onChange={handleChange} placeholder="Name" />
                  <input className="form-control mb-2" name="hospital" value={form.hospital} onChange={handleChange} placeholder="Hospital" />
                  <input className="form-control mb-2" name="specialization" value={form.specialization} onChange={handleChange} placeholder="Specialization" />
                  <input className="form-control mb-2" name="department" value={form.department} onChange={handleChange} placeholder="Department" />
                  <input className="form-control mb-2" name="designation" value={form.designation} onChange={handleChange} placeholder="Designation" />
                  <input className="form-control mb-2" name="experience" value={form.experience} onChange={handleChange} placeholder="Experience" type="number" />
                  <input className="form-control mb-2" name="email" value={form.email} onChange={handleChange} placeholder="Email" />
                  <button className="btn btn-success me-2" onClick={handleSave}>Save</button>
                  <button className="btn btn-secondary" onClick={handleCancel}>Cancel</button>
                </>
              ) : profile && (
                <>
                  <div><b>Name:</b> {profile.name}</div>
                  <div><b>Hospital:</b> {profile.hospital}</div>
                  <div><b>Specialization:</b> {profile.specialization}</div>
                  <div><b>Department:</b> {profile.department}</div>
                  <div><b>Designation:</b> {profile.designation}</div>
                  <div><b>Experience:</b> {profile.experience && profile.experience.toString()} years</div>
                  <div><b>Email:</b> {profile.email}</div>
                </>
              )}
            </div>
          </div>
          <div className="card mb-4">
            <div className="card-header">Consultancy Reports</div>
            <div className="card-body">
              <ul className="list-group">
                {reports.map((rep, i) => {
                  const pat = patientDetailsMap[rep.patient.toLowerCase()];
                  return (
                    <li className="list-group-item" key={i}>
                      <div><b>Patient Name:</b> {pat ? pat.name : rep.patient}</div>
                      <div><b>H Number:</b> {pat ? pat.hNumber : ''}</div>
                      <div><b>Wallet:</b> {rep.patient}</div>
                      <div><b>Diagnosis:</b> {rep.diagnosis}</div>
                      <div><b>Prescription:</b> {rep.prescription}</div>
                      <div><b>Date:</b> {rep.timestamp && new Date(Number(rep.timestamp.toString()) * 1000).toLocaleString()}</div>
                    </li>
                  );
                })}
              </ul>
            </div>
          </div>
        </div>
        <div className="col-md-6">
          <div className="card mb-4">
            <div className="card-header">Active Patient List</div>
            <div className="card-body p-0">
              <div style={{overflowX: 'auto'}}>
                <table className="table table-bordered table-sm mb-0 align-middle text-center" style={{minWidth: 400}}>
                  <thead className="table-light">
                    <tr>
                      <th>Name</th>
                      <th>H Number</th>
                      <th>Wallet</th>
                      <th>Status</th>
                    </tr>
                  </thead>
                  <tbody>
                    {patients.map((pat, i) => {
                      const p = patientDetailsMap[pat.patient.toLowerCase()];
                      return (
                        <tr key={i}>
                          <td style={{whiteSpace: 'nowrap', textOverflow: 'ellipsis', overflow: 'hidden', maxWidth: 120}}>{p ? p.name : pat.patient}</td>
                          <td>{p ? p.hNumber : ''}</td>
                          <td style={{fontSize: '0.95em', wordBreak: 'break-all'}}>{pat.patient}</td>
                          <td>{pat.active ? 'Active' : 'Revoked'}</td>
                        </tr>
                      );
                    })}
                  </tbody>
                </table>
              </div>
            </div>
          </div>
          <div className="card mb-4">
            <div className="card-header">Patient List</div>
            <div className="card-body">
              <div className="mb-3">
                <select className="form-select mb-2" value={selectedPatient} onChange={e => setSelectedPatient(e.target.value)}>
                  <option value="">Select Patient for Report</option>
                  {patients.map((pat, i) => {
                    const p = patientDetailsMap[pat.patient.toLowerCase()];
                    return (
                      <option key={i} value={pat.patient}>
                        {p ? `${p.name} (${p.hNumber})` : pat.patient}
                      </option>
                    );
                  })}
                </select>
                <Button className="btn btn-info mb-2" onClick={handleViewRecords}>View Selected Patient Records</Button>
                {recordStatus && <div className="alert alert-warning mt-2">{recordStatus}</div>}
                {patientRecords.length > 0 && <h6>Patient Records:</h6>}
                <ul className="list-group mt-2">
                  {patientRecords.map((rec, i) => (
                    <li className="list-group-item" key={i}>
                      <div><b>Type:</b> {rec.recordType}</div>
                      <div><b>Date:</b> {rec.timestamp && new Date(Number(rec.timestamp.toString()) * 1000).toLocaleString()}</div>
                      <div>
                        <b>IPFS:</b> <Button variant="outline-primary" size="sm" className="ms-2" onClick={() => window.open(`https://ipfs.io/ipfs/${rec.ipfsHash}`, '_blank', 'noopener,noreferrer')}>View Record</Button>
                      </div>
                    </li>
                  ))}
                </ul>
                {patientDiagnosticReports.length > 0 && <h6 className="mt-3">Patient Diagnostic Reports:</h6>}
                <ul className="list-group">
                  {patientDiagnosticReports.map((rep, i) => (
                    <li className="list-group-item" key={i}>
                      <div><b>Test Type:</b> {rep.testType}</div>
                      <div><b>Date:</b> {rep.timestamp && new Date(Number(rep.timestamp.toString()) * 1000).toLocaleString()}</div>
                      <div>
                        <b>IPFS:</b> <Button variant="outline-primary" size="sm" className="ms-2" onClick={() => window.open(`https://ipfs.io/ipfs/${rep.ipfsHash}`, '_blank', 'noopener,noreferrer')}>View Record</Button>
                      </div>
                      <div><b>Diagnostic Center:</b> {rep.centerName || 'Unknown Center'}</div>
                    </li>
                  ))}
                </ul>
                <input className="form-control mb-2" placeholder="Diagnosis" value={diagnosis} onChange={e => setDiagnosis(e.target.value)} />
                <input className="form-control mb-2" placeholder="Prescription" value={prescription} onChange={e => setPrescription(e.target.value)} />
                <Button className="btn btn-primary" onClick={handleReport}>Create Consultancy Report</Button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default DoctorDashboard; 