import React from 'react';
import { useNavigate } from 'react-router-dom';

const roles = [
  {
    key: 'patient',
    title: 'Patient',
    icon: 'bi-person-circle',
    description: 'Login to view and manage your health records.'
  },
  {
    key: 'doctor',
    title: 'Doctor',
    icon: 'bi-clipboard2-pulse',
    description: 'Login to access patient records and create reports.'
  },
  {
    key: 'diagnostic',
    title: 'Diagnostic Center',
    icon: 'bi-hospital',
    description: 'Login to upload and manage diagnostic reports.'
  }
];

const LoginPage = () => {
  const navigate = useNavigate();
  return (
    <div className="container mt-5">
      <h2 className="mb-4 text-center">Choose Your Role to Login</h2>
      <div className="row justify-content-center">
        {roles.map(role => (
          <div className="col-md-4 mb-4" key={role.key}>
            <div className="card h-100 shadow-sm text-center">
              <div className="card-body">
                <i className={`bi ${role.icon} display-1 mb-3`}></i>
                <h5 className="card-title">{role.title}</h5>
                <p className="card-text">{role.description}</p>
                <button className="btn btn-success mt-2" onClick={() => navigate(`/login/${role.key}`)}>
                  Login as {role.title}
                </button>
                <div className="mt-3">
                  <span>Don't have an account? </span>
                  <button className="btn btn-link p-0" onClick={() => navigate('/register')}>Register here</button>
                </div>
              </div>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

export default LoginPage; 