import React, { useState, useEffect } from 'react';
import { ethers } from 'ethers';
import DiagnosticContractABI from '../abis/DiagnosticContract.json';
import contractAddresses from '../config/contractAddresses.json';
import { uploadFile } from '../utils/uploadFile';
import EHRSystemABI from '../abis/EHRSystem.json';
import PatientContractABI from '../abis/PatientContract.json';

const DiagnosticDashboard = () => {
  const [profile, setProfile] = useState(null);
  const [editMode, setEditMode] = useState(false);
  const [form, setForm] = useState({});
  const [reports, setReports] = useState([]);
  const [patients, setPatients] = useState([]);
  const [patientDetailsMap, setPatientDetailsMap] = useState({});
  const [testType, setTestType] = useState('');
  const [ipfsFile, setIpfsFile] = useState(null);
  const [txStatus, setTxStatus] = useState('');
  const [loading, setLoading] = useState(true);
  const [patientAddr, setPatientAddr] = useState('');

  const user = JSON.parse(localStorage.getItem('ehr_user'));

  useEffect(() => {
    if (!user || user.role !== 'diagnostic') {
      window.location.href = '/login/diagnostic';
      return;
    }
    loadData();
    fetchPatients();
    // eslint-disable-next-line
  }, []);

  const loadData = async () => {
    setLoading(true);
    try {
      const provider = new ethers.providers.Web3Provider(window.ethereum);
      const signer = provider.getSigner();
      const contract = new ethers.Contract(contractAddresses.DiagnosticContract, DiagnosticContractABI.abi, signer);
      const c = await contract.centers(user.wallet);
      setProfile(c);
      setForm({
        name: c.name,
        location: c.location,
        contactNumber: c.contactNumber,
        email: c.email,
        licenseNumber: c.licenseNumber,
        establishedDate: c.establishedDate
      });
      const reps = await contract.getReportsByCenter(user.wallet);
      setReports(reps);
    } catch (err) {
      setTxStatus('Error loading data: ' + err.message);
    }
    setLoading(false);
  };

  const fetchPatients = async () => {
    try {
      const provider = new ethers.providers.Web3Provider(window.ethereum);
      const signer = provider.getSigner();
      const ehrSystem = new ethers.Contract(contractAddresses.EHRSystem, EHRSystemABI.abi, signer);
      const patientContract = new ethers.Contract(contractAddresses.PatientContract, PatientContractABI.abi, signer);
      const allPatients = await ehrSystem.getAllPatients();
      const detailsMap = {};
      await Promise.all(allPatients.map(async addr => {
        try {
          const p = await patientContract.patients(addr);
          detailsMap[addr.toLowerCase()] = {
            name: p.name,
            hNumber: p.hNumber,
            wallet: addr
          };
        } catch {}
      }));
      setPatientDetailsMap(detailsMap);
      setPatients(allPatients);
    } catch (err) {
      // handle error if needed
    }
  };

  const handleEdit = () => setEditMode(true);
  const handleCancel = () => setEditMode(false);
  const handleChange = e => setForm(f => ({ ...f, [e.target.name]: e.target.value }));

  const handleSave = async () => {
    setTxStatus('');
    try {
      const provider = new ethers.providers.Web3Provider(window.ethereum);
      const signer = provider.getSigner();
      const contract = new ethers.Contract(contractAddresses.DiagnosticContract, DiagnosticContractABI.abi, signer);
      const tx = await contract.updateProfile(
        form.name,
        form.location,
        form.contactNumber,
        form.email,
        form.licenseNumber,
        form.establishedDate,
        [] // servicesOffered update omitted for brevity
      );
      setTxStatus('Profile update sent. Waiting for confirmation...');
      await tx.wait();
      setTxStatus('Profile updated!');
      setEditMode(false);
      loadData();
    } catch (err) {
      setTxStatus('Profile update failed: ' + err.message);
    }
  };

  const handleFileChange = e => setIpfsFile(e.target.files[0]);

  const handleCreateReport = async () => {
    setTxStatus('');
    if (!patientAddr || !testType || !ipfsFile) {
      setTxStatus('Fill all fields and select a file.');
      return;
    }
    try {
      setTxStatus('Uploading file to Pinata...');
      const hash = await uploadFile(ipfsFile);
      setTxStatus('File uploaded to Pinata. Storing on blockchain...');
      const providerEth = new ethers.providers.Web3Provider(window.ethereum);
      const signer = providerEth.getSigner();
      const contract = new ethers.Contract(contractAddresses.DiagnosticContract, DiagnosticContractABI.abi, signer);
      const tx = await contract.createDiagnosticReport(patientAddr, patientAddr, testType, hash);
      setTxStatus('Report creation sent. Waiting for confirmation...');
      await tx.wait();
      setTxStatus('Diagnostic report created!');
      setPatientAddr('');
      setTestType('');
      setIpfsFile(null);
      loadData();
    } catch (err) {
      setTxStatus('Report creation failed: ' + err.message);
    }
  };

  if (loading) return <div className="container mt-5 text-center">Loading...</div>;

  return (
    <div className="container mt-5">
      <h2 className="mb-4">Diagnostic Center Dashboard</h2>
      {txStatus && <div className="alert alert-info">{txStatus}</div>}
      <div className="row">
        <div className="col-md-6">
          <div className="card mb-4">
            <div className="card-header d-flex justify-content-between align-items-center">
              <span>Profile</span>
              {!editMode && <button className="btn btn-sm btn-outline-primary" onClick={handleEdit}>Edit</button>}
            </div>
            <div className="card-body">
              {editMode ? (
                <>
                  <input className="form-control mb-2" name="name" value={form.name} onChange={handleChange} placeholder="Name" />
                  <input className="form-control mb-2" name="location" value={form.location} onChange={handleChange} placeholder="Location" />
                  <input className="form-control mb-2" name="contactNumber" value={form.contactNumber} onChange={handleChange} placeholder="Contact Number" />
                  <input className="form-control mb-2" name="email" value={form.email} onChange={handleChange} placeholder="Email" />
                  <input className="form-control mb-2" name="licenseNumber" value={form.licenseNumber} onChange={handleChange} placeholder="License Number" />
                  <input className="form-control mb-2" name="establishedDate" value={form.establishedDate} onChange={handleChange} placeholder="Established Date" />
                  <button className="btn btn-success me-2" onClick={handleSave}>Save</button>
                  <button className="btn btn-secondary" onClick={handleCancel}>Cancel</button>
                </>
              ) : profile && (
                <>
                  <div><b>Name:</b> {profile.name}</div>
                  <div><b>Location:</b> {profile.location}</div>
                  <div><b>Contact Number:</b> {profile.contactNumber}</div>
                  <div><b>Email:</b> {profile.email}</div>
                  <div><b>License Number:</b> {profile.licenseNumber}</div>
                  <div><b>Established Date:</b> {profile.establishedDate}</div>
                </>
              )}
            </div>
          </div>
          <div className="card mb-4">
            <div className="card-header">Diagnostic Reports</div>
            <div className="card-body">
              <ul className="list-group">
                {reports.map((rep, i) => (
                  <li className="list-group-item" key={i}>
                    <div><b>Type:</b> {rep.testType}</div>
                    <div><b>Date:</b> {rep.timestamp && new Date(Number(rep.timestamp.toString()) * 1000).toLocaleString()}</div>
                    <div>
                      <b>IPFS:</b> <button className="btn btn-outline-primary btn-sm ms-2" onClick={() => window.open(`https://ipfs.io/ipfs/${rep.ipfsHash}`, '_blank', 'noopener,noreferrer')}>View Record</button>
                    </div>
                  </li>
                ))}
              </ul>
            </div>
          </div>
        </div>
        <div className="col-md-6">
          <div className="card mb-4">
            <div className="card-header">Create Diagnostic Report</div>
            <div className="card-body">
              <select className="form-select mb-2" value={patientAddr} onChange={e => setPatientAddr(e.target.value)}>
                <option value="">Select Patient</option>
                {patients.map(addr => {
                  const details = patientDetailsMap[addr.toLowerCase()];
                  return details ? (
                    <option key={addr} value={addr}>
                      {details.name} ({details.hNumber})
                    </option>
                  ) : null;
                })}
              </select>
              <input className="form-control mb-2" placeholder="Test Type (e.g., MRI)" value={testType} onChange={e => setTestType(e.target.value)} />
              <input type="file" className="form-control mb-2" onChange={handleFileChange} />
              <button className="btn btn-primary" onClick={handleCreateReport}>Create Report</button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default DiagnosticDashboard; 