import React from 'react';
import { useNavigate } from 'react-router-dom';

const roles = [
  {
    key: 'patient',
    title: 'Patient',
    icon: 'bi-person-circle',
    description: 'Upload records, grant/revoke permissions, view your data.'
  },
  {
    key: 'doctor',
    title: 'Doctor',
    icon: 'bi-clipboard2-pulse',
    description: 'Access permitted patient records, create consultancy reports.'
  },
  {
    key: 'diagnostic',
    title: 'Diagnostic Center',
    icon: 'bi-hospital',
    description: 'Create diagnostic reports, upload test results.'
  }
];

const RegistrationPage = () => {
  const navigate = useNavigate();
  return (
    <div className="container mt-5">
      <h2 className="mb-4 text-center">Choose Your Role to Register</h2>
      <div className="row justify-content-center">
        {roles.map(role => (
          <div className="col-md-4 mb-4" key={role.key}>
            <div className="card h-100 shadow-sm text-center">
              <div className="card-body">
                <i className={`bi ${role.icon} display-1 mb-3`}></i>
                <h5 className="card-title">{role.title}</h5>
                <p className="card-text">{role.description}</p>
                <button className="btn btn-primary mt-2" onClick={() => navigate(`/register/${role.key}`)}>
                  Register as {role.title}
                </button>
              </div>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

export default RegistrationPage; 