import React from 'react';

const AboutUsPage = () => (
  <div className="container mt-5">
    <h2>About Us</h2>
    <p>
      This project is a decentralized Electronic Health Records (EHR) system leveraging Ethereum blockchain and IPFS for secure, patient-controlled healthcare data management.
    </p>
    <h4>Technology Stack</h4>
    <ul>
      <li>Ethereum Smart Contracts (Solidity)</li>
      <li>IPFS for decentralized file storage</li>
      <li>React.js for frontend</li>
      <li>Web3.js/Ethers.js for blockchain interaction</li>
      <li>Bootstrap for UI</li>
    </ul>
    <h4>Team</h4>
    <p>Developed by the ABC University Minor Project Team.</p>
    <h4>Contact</h4>
    <p>Email: contact@abcuniversity.edu</p>
    <h4>Vision</h4>
    <p>Empowering patients and healthcare providers with secure, interoperable, and privacy-focused health data management.</p>
  </div>
);

export default AboutUsPage; 