import React from 'react';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import Navbar from './components/Navbar';
import HomePage from './pages/HomePage';
import AboutUsPage from './pages/AboutUsPage';
import RegistrationPage from './pages/RegistrationPage';
import LoginPage from './pages/LoginPage';
import PatientRegistrationForm from './pages/PatientRegistrationForm';
import DoctorRegistrationForm from './pages/DoctorRegistrationForm';
import DiagnosticRegistrationForm from './pages/DiagnosticRegistrationForm';
import PatientLoginForm from './pages/PatientLoginForm';
import DoctorLoginForm from './pages/DoctorLoginForm';
import DiagnosticLoginForm from './pages/DiagnosticLoginForm';
import PatientDashboard from './pages/PatientDashboard';
import DoctorDashboard from './pages/DoctorDashboard';
import DiagnosticDashboard from './pages/DiagnosticDashboard';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap-icons/font/bootstrap-icons.css';

function App() {
  return (
    <Router>
      <Navbar />
      <Routes>
        <Route path="/" element={<HomePage />} />
        <Route path="/about" element={<AboutUsPage />} />
        <Route path="/register" element={<RegistrationPage />} />
        <Route path="/register/patient" element={<PatientRegistrationForm />} />
        <Route path="/register/doctor" element={<DoctorRegistrationForm />} />
        <Route path="/register/diagnostic" element={<DiagnosticRegistrationForm />} />
        <Route path="/login" element={<LoginPage />} />
        <Route path="/login/patient" element={<PatientLoginForm />} />
        <Route path="/login/doctor" element={<DoctorLoginForm />} />
        <Route path="/login/diagnostic" element={<DiagnosticLoginForm />} />
        <Route path="/dashboard/patient" element={<PatientDashboard />} />
        <Route path="/dashboard/doctor" element={<DoctorDashboard />} />
        <Route path="/dashboard/diagnostic" element={<DiagnosticDashboard />} />
        {/* Add role-specific registration and login routes here */}
      </Routes>
    </Router>
  );
}

export default App;
