import React, { useState, useEffect } from 'react';
import { ethers } from 'ethers';
// TODO: Replace with actual ABI and contract address
import PatientContractABI from '../abis/PatientContract.json';
import contractAddresses from '../config/contractAddresses.json';

const initialState = {
  wallet: '',
  name: '',
  gender: '',
  bloodGroup: '',
  homeAddress: '',
  dateOfBirth: '',
  email: '',
  hNumber: '',
  password: '',
  confirmPassword: '',
};

const bloodGroups = [
  'A+', 'B+', 'AB+', 'O+', 'A-', 'B-', 'AB-', 'O-'
];

const genders = ['Male', 'Female', 'Other'];

const PatientRegistrationForm = () => {
  const [form, setForm] = useState(initialState);
  const [errors, setErrors] = useState({});
  const [metaMaskConnected, setMetaMaskConnected] = useState(false);
  const [txStatus, setTxStatus] = useState('');
  const [txHash, setTxHash] = useState('');

  // MetaMask wallet autofill
  useEffect(() => {
    if (window.ethereum) {
      window.ethereum.request({ method: 'eth_accounts' }).then(accounts => {
        if (accounts.length > 0) {
          setForm(f => ({ ...f, wallet: accounts[0] }));
          setMetaMaskConnected(true);
        }
      });
      // Listen for account changes
      const handleAccountsChanged = (accounts) => {
        if (accounts.length > 0) {
          setForm(f => ({ ...f, wallet: accounts[0] }));
        } else {
          setForm(f => ({ ...f, wallet: '' }));
        }
      };
      window.ethereum.on('accountsChanged', handleAccountsChanged);
      return () => {
        window.ethereum.removeListener('accountsChanged', handleAccountsChanged);
      };
    }
  }, []);

  // Real-time validation
  useEffect(() => {
    validateForm();
    // eslint-disable-next-line
  }, [form]);

  const validateForm = () => {
    const newErrors = {};
    if (!form.wallet) newErrors.wallet = 'Connect MetaMask to autofill wallet address.';
    if (!form.name) newErrors.name = 'Name is required.';
    if (!form.gender) newErrors.gender = 'Gender is required.';
    if (!form.bloodGroup) newErrors.bloodGroup = 'Blood group is required.';
    if (!form.homeAddress) newErrors.homeAddress = 'Home address is required.';
    if (!form.dateOfBirth) newErrors.dateOfBirth = 'Date of birth is required.';
    else if (new Date(form.dateOfBirth) > new Date()) newErrors.dateOfBirth = 'Birth date cannot be in the future.';
    if (!form.email) newErrors.email = 'Email is required.';
    else if (!/^\S+@\S+\.\S+$/.test(form.email)) newErrors.email = 'Invalid email format.';
    if (!form.hNumber) newErrors.hNumber = 'H Number is required.';
    else if (!/^\d{6}$/.test(form.hNumber)) newErrors.hNumber = 'H Number must be exactly 6 digits.';
    if (!form.password) newErrors.password = 'Password is required.';
    else if (form.password.length < 8) newErrors.password = 'Password must be at least 8 characters.';
    if (form.password !== form.confirmPassword) newErrors.confirmPassword = 'Passwords do not match.';
    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleChange = e => {
    const { name, value } = e.target;
    setForm(f => ({ ...f, [name]: value }));
  };

  const handleSubmit = async e => {
    e.preventDefault();
    if (!validateForm()) return;
    setTxStatus('');
    setTxHash('');
    try {
      if (!window.ethereum) throw new Error('MetaMask not detected');
      setTxStatus('Waiting for wallet confirmation...');
      const provider = new ethers.providers.Web3Provider(window.ethereum);
      const signer = provider.getSigner();
      const contract = new ethers.Contract(
        contractAddresses.PatientContract,
        PatientContractABI.abi,
        signer
      );
      const tx = await contract.registerPatient(
        form.hNumber,
        form.name,
        form.bloodGroup,
        form.homeAddress,
        form.dateOfBirth,
        form.gender,
        form.email,
        ethers.utils.keccak256(ethers.utils.toUtf8Bytes(form.password))
      );
      setTxStatus('Transaction sent. Waiting for confirmation...');
      setTxHash(tx.hash);
      await tx.wait();
      setTxStatus('Registration successful!');
    } catch (err) {
      setTxStatus('Registration failed: ' + (err.reason || err.message));
    }
  };

  return (
    <div className="container mt-5">
      <h2 className="mb-4 text-center">Patient Registration</h2>
      <form className="card p-4 shadow-sm mx-auto" style={{ maxWidth: 600 }} onSubmit={handleSubmit} autoComplete="off">
        <div className="mb-3">
          <label className="form-label">Wallet Public Address</label>
          <input type="text" className={`form-control ${errors.wallet ? 'is-invalid' : ''}`} name="wallet" value={form.wallet} disabled />
          {errors.wallet && <div className="invalid-feedback">{errors.wallet}</div>}
          {!metaMaskConnected && <div className="form-text text-danger">MetaMask not connected</div>}
        </div>
        <div className="mb-3">
          <label className="form-label">Patient Name</label>
          <input type="text" className={`form-control ${errors.name ? 'is-invalid' : ''}`} name="name" value={form.name} onChange={handleChange} />
          {errors.name && <div className="invalid-feedback">{errors.name}</div>}
        </div>
        <div className="mb-3">
          <label className="form-label">Gender</label>
          <select className={`form-select ${errors.gender ? 'is-invalid' : ''}`} name="gender" value={form.gender} onChange={handleChange}>
            <option value="">Select gender</option>
            {genders.map(gender => (
              <option key={gender} value={gender}>{gender}</option>
            ))}
          </select>
          {errors.gender && <div className="invalid-feedback">{errors.gender}</div>}
        </div>
        <div className="mb-3">
          <label className="form-label">Blood Group</label>
          <select className={`form-select ${errors.bloodGroup ? 'is-invalid' : ''}`} name="bloodGroup" value={form.bloodGroup} onChange={handleChange}>
            <option value="">Select blood group</option>
            {bloodGroups.map(group => (
              <option key={group} value={group}>{group}</option>
            ))}
          </select>
          {errors.bloodGroup && <div className="invalid-feedback">{errors.bloodGroup}</div>}
        </div>
        <div className="mb-3">
          <label className="form-label">Home Address</label>
          <input type="text" className={`form-control ${errors.homeAddress ? 'is-invalid' : ''}`} name="homeAddress" value={form.homeAddress} onChange={handleChange} />
          {errors.homeAddress && <div className="invalid-feedback">{errors.homeAddress}</div>}
        </div>
        <div className="mb-3">
          <label className="form-label">Date of Birth</label>
          <input type="date" className={`form-control ${errors.dateOfBirth ? 'is-invalid' : ''}`} name="dateOfBirth" value={form.dateOfBirth} onChange={handleChange} />
          {errors.dateOfBirth && <div className="invalid-feedback">{errors.dateOfBirth}</div>}
        </div>
        <div className="mb-3">
          <label className="form-label">Email</label>
          <input type="email" className={`form-control ${errors.email ? 'is-invalid' : ''}`} name="email" value={form.email} onChange={handleChange} />
          {errors.email && <div className="invalid-feedback">{errors.email}</div>}
        </div>
        <div className="mb-3">
          <label className="form-label">H Number</label>
          <input type="text" className={`form-control ${errors.hNumber ? 'is-invalid' : ''}`} name="hNumber" value={form.hNumber} onChange={handleChange} />
          {errors.hNumber && <div className="invalid-feedback">{errors.hNumber}</div>}
        </div>
        <div className="mb-3">
          <label className="form-label">Password</label>
          <input type="password" className={`form-control ${errors.password ? 'is-invalid' : ''}`} name="password" value={form.password} onChange={handleChange} />
          {errors.password && <div className="invalid-feedback">{errors.password}</div>}
        </div>
        <div className="mb-3">
          <label className="form-label">Confirm Password</label>
          <input type="password" className={`form-control ${errors.confirmPassword ? 'is-invalid' : ''}`} name="confirmPassword" value={form.confirmPassword} onChange={handleChange} />
          {errors.confirmPassword && <div className="invalid-feedback">{errors.confirmPassword}</div>}
        </div>
        <button type="submit" className="btn btn-primary">Register</button>
      </form>
      {txStatus && <div className="mt-3 text-center">{txStatus}</div>}
      {txHash && <div className="mt-3 text-center">Transaction Hash: {txHash}</div>}
    </div>
  );
};

export default PatientRegistrationForm;