import React, { useState, useEffect } from 'react';
import { ethers } from 'ethers';
import PatientContractABI from '../abis/PatientContract.json';
import contractAddresses from '../config/contractAddresses.json';

const PatientLoginForm = () => {
  const [wallet, setWallet] = useState('');
  const [hNumber, setHNumber] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState('');
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    if (window.ethereum) {
      window.ethereum.request({ method: 'eth_accounts' }).then(accounts => {
        if (accounts.length > 0) setWallet(accounts[0]);
      });
      // Listen for account changes
      const handleAccountsChanged = (accounts) => {
        if (accounts.length > 0) {
          setWallet(accounts[0]);
        } else {
          setWallet('');
        }
      };
      window.ethereum.on('accountsChanged', handleAccountsChanged);
      return () => {
        window.ethereum.removeListener('accountsChanged', handleAccountsChanged);
      };
    }
  }, []);

  const handleSubmit = async e => {
    e.preventDefault();
    setError('');
    setLoading(true);
    try {
      if (!window.ethereum) throw new Error('MetaMask not detected');
      const provider = new ethers.providers.Web3Provider(window.ethereum);
      const contract = new ethers.Contract(
        contractAddresses.PatientContract,
        PatientContractABI.abi,
        provider
      );
      const patient = await contract.patients(wallet);
      if (!patient.exists) throw new Error('No patient registered with this wallet');
      if (patient.hNumber !== hNumber) throw new Error('Invalid H Number');
      const passwordHash = ethers.utils.keccak256(ethers.utils.toUtf8Bytes(password));
      if (patient.passwordHash !== passwordHash) throw new Error('Invalid password');
      localStorage.setItem('ehr_user', JSON.stringify({ role: 'patient', wallet, hNumber }));
      window.location.href = '/dashboard/patient';
    } catch (err) {
      setError(err.message);
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="container mt-5">
      <h2 className="mb-4 text-center">Patient Login</h2>
      <form className="card p-4 shadow-sm mx-auto" style={{ maxWidth: 400 }} onSubmit={handleSubmit} autoComplete="off">
        <div className="mb-3">
          <label className="form-label">Wallet Address</label>
          <input type="text" className="form-control" value={wallet} disabled />
        </div>
        <div className="mb-3">
          <label className="form-label">H Number</label>
          <input type="text" className="form-control" value={hNumber} onChange={e => setHNumber(e.target.value)} maxLength={6} required />
        </div>
        <div className="mb-3">
          <label className="form-label">Password</label>
          <input type="password" className="form-control" value={password} onChange={e => setPassword(e.target.value)} required />
        </div>
        {error && <div className="alert alert-danger">{error}</div>}
        <button type="submit" className="btn btn-success w-100" disabled={loading}>{loading ? 'Logging in...' : 'Login'}</button>
      </form>
    </div>
  );
};

export default PatientLoginForm; 