import React from 'react';

const HomePage = () => (
  <div className="container mt-5">
    <div className="jumbotron text-center bg-primary text-white rounded p-5">
      <h1 className="display-4">Welcome to the Decentralized EHR System</h1>
      <p className="lead">Secure, patient-controlled, and blockchain-powered healthcare records management.</p>
      <hr className="my-4" />
      <p>Get started by connecting your MetaMask wallet and registering your role.</p>
      <div className="mt-4">
        {/* MetaMask connection status will be shown here */}
        <span className="badge bg-warning text-dark">MetaMask: Not Connected</span>
      </div>
    </div>
    <div className="row mt-5">
      <div className="col-md-4">
        <div className="card shadow-sm">
          <div className="card-body">
            <h5 className="card-title">Blockchain Security</h5>
            <p className="card-text">Your health records are stored securely and immutably on the Ethereum blockchain.</p>
          </div>
        </div>
      </div>
      <div className="col-md-4">
        <div className="card shadow-sm">
          <div className="card-body">
            <h5 className="card-title">Patient Control</h5>
            <p className="card-text">You decide who can access your records with granular permission management.</p>
          </div>
        </div>
      </div>
      <div className="col-md-4">
        <div className="card shadow-sm">
          <div className="card-body">
            <h5 className="card-title">Interoperability</h5>
            <p className="card-text">Seamless sharing between patients, doctors, and diagnostic centers.</p>
          </div>
        </div>
      </div>
    </div>
  </div>
);

export default HomePage; 