import React, { useState, useEffect } from 'react';
import { ethers } from 'ethers';
// TODO: Replace with actual ABI and contract address
import DoctorContractABI from '../abis/DoctorContract.json';
import contractAddresses from '../config/contractAddresses.json';

const initialState = {
  wallet: '',
  name: '',
  hospital: '',
  location: '',
  dateOfBirth: '',
  gender: '',
  email: '',
  hNumber: '',
  specialization: '',
  department: '',
  designation: '',
  experience: '',
  password: '',
  confirmPassword: '',
};

const genders = ['Male', 'Female', 'Other'];
const specializations = [
  'Dermatology', 'Cardiology', 'Neurology', 'Orthopedics', 'Pediatrics', 'General Medicine', 'Radiology', 'Oncology', 'Other'
];
const designations = ['Doctor', 'Senior Doctor', 'Consultant', 'Resident', 'Other'];

const DoctorRegistrationForm = () => {
  const [form, setForm] = useState(initialState);
  const [errors, setErrors] = useState({});
  const [metaMaskConnected, setMetaMaskConnected] = useState(false);
  const [txStatus, setTxStatus] = useState('');
  const [txHash, setTxHash] = useState('');

  useEffect(() => {
    if (window.ethereum) {
      window.ethereum.request({ method: 'eth_accounts' }).then(accounts => {
        if (accounts.length > 0) {
          setForm(f => ({ ...f, wallet: accounts[0] }));
          setMetaMaskConnected(true);
        }
      });
      // Listen for account changes
      const handleAccountsChanged = (accounts) => {
        if (accounts.length > 0) {
          setForm(f => ({ ...f, wallet: accounts[0] }));
        } else {
          setForm(f => ({ ...f, wallet: '' }));
        }
      };
      window.ethereum.on('accountsChanged', handleAccountsChanged);
      return () => {
        window.ethereum.removeListener('accountsChanged', handleAccountsChanged);
      };
    }
  }, []);

  useEffect(() => {
    validateForm();
    // eslint-disable-next-line
  }, [form]);

  const validateForm = () => {
    const newErrors = {};
    if (!form.wallet) newErrors.wallet = 'Connect MetaMask to autofill wallet address.';
    if (!form.name) newErrors.name = 'Name is required.';
    if (!form.hospital) newErrors.hospital = 'Hospital name is required.';
    if (!form.location) newErrors.location = 'Location is required.';
    if (!form.dateOfBirth) newErrors.dateOfBirth = 'Date of birth is required.';
    else if (new Date(form.dateOfBirth) > new Date()) newErrors.dateOfBirth = 'Birth date cannot be in the future.';
    if (!form.gender) newErrors.gender = 'Gender is required.';
    if (!form.email) newErrors.email = 'Email is required.';
    else if (!/^\S+@\S+\.\S+$/.test(form.email)) newErrors.email = 'Invalid email format.';
    if (!form.hNumber) newErrors.hNumber = 'H Number is required.';
    else if (!/^\d{6}$/.test(form.hNumber)) newErrors.hNumber = 'H Number must be exactly 6 digits.';
    if (!form.specialization) newErrors.specialization = 'Specialization is required.';
    if (!form.department) newErrors.department = 'Department is required.';
    if (!form.designation) newErrors.designation = 'Designation is required.';
    if (!form.experience) newErrors.experience = 'Work experience is required.';
    else if (isNaN(form.experience) || Number(form.experience) < 0) newErrors.experience = 'Experience must be a positive number.';
    if (!form.password) newErrors.password = 'Password is required.';
    else if (form.password.length < 8) newErrors.password = 'Password must be at least 8 characters.';
    if (form.password !== form.confirmPassword) newErrors.confirmPassword = 'Passwords do not match.';
    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleChange = e => {
    const { name, value } = e.target;
    setForm(f => ({ ...f, [name]: value }));
  };

  const handleSubmit = async e => {
    e.preventDefault();
    if (!validateForm()) return;
    setTxStatus('');
    setTxHash('');
    try {
      if (!window.ethereum) throw new Error('MetaMask not detected');
      setTxStatus('Waiting for wallet confirmation...');
      const provider = new ethers.providers.Web3Provider(window.ethereum);
      const signer = provider.getSigner();
      const contract = new ethers.Contract(
        contractAddresses.DoctorContract,
        DoctorContractABI.abi,
        signer
      );
      // Debug log all arguments before contract call
      console.log('registerDoctor arguments:',
        form.hNumber,
        form.name,
        form.hospital,
        form.specialization,
        form.department,
        form.designation,
        Number(form.experience),
        form.dateOfBirth,
        form.gender,
        form.email,
        ethers.utils.keccak256(ethers.utils.toUtf8Bytes(form.password))
      );
      const tx = await contract.registerDoctor(
        form.hNumber,
        form.name,
        form.hospital,
        form.specialization,
        form.department,
        form.designation,
        Number(form.experience),
        form.dateOfBirth,
        form.gender,
        form.email,
        ethers.utils.keccak256(ethers.utils.toUtf8Bytes(form.password))
      );
      setTxStatus('Transaction sent. Waiting for confirmation...');
      setTxHash(tx.hash);
      await tx.wait();
      setTxStatus('Registration successful!');
    } catch (err) {
      setTxStatus('Registration failed: ' + (err.reason || err.message));
    }
  };

  return (
    <div className="container mt-5">
      <h2 className="mb-4 text-center">Doctor Registration</h2>
      <form className="card p-4 shadow-sm mx-auto" style={{ maxWidth: 700 }} onSubmit={handleSubmit} autoComplete="off">
        <div className="mb-3">
          <label className="form-label">Wallet Public Address</label>
          <input type="text" className={`form-control ${errors.wallet ? 'is-invalid' : ''}`} name="wallet" value={form.wallet} disabled />
          {errors.wallet && <div className="invalid-feedback">{errors.wallet}</div>}
          {!metaMaskConnected && <div className="form-text text-danger">MetaMask not connected</div>}
        </div>
        <div className="mb-3">
          <label className="form-label">Doctor Name</label>
          <input type="text" className={`form-control ${errors.name ? 'is-invalid' : ''}`} name="name" value={form.name} onChange={handleChange} />
          {errors.name && <div className="invalid-feedback">{errors.name}</div>}
        </div>
        <div className="mb-3">
          <label className="form-label">Hospital Name</label>
          <input type="text" className={`form-control ${errors.hospital ? 'is-invalid' : ''}`} name="hospital" value={form.hospital} onChange={handleChange} />
          {errors.hospital && <div className="invalid-feedback">{errors.hospital}</div>}
        </div>
        <div className="mb-3">
          <label className="form-label">Location</label>
          <input type="text" className={`form-control ${errors.location ? 'is-invalid' : ''}`} name="location" value={form.location} onChange={handleChange} />
          {errors.location && <div className="invalid-feedback">{errors.location}</div>}
        </div>
        <div className="mb-3">
          <label className="form-label">Date of Birth</label>
          <input type="date" className={`form-control ${errors.dateOfBirth ? 'is-invalid' : ''}`} name="dateOfBirth" value={form.dateOfBirth} onChange={handleChange} />
          {errors.dateOfBirth && <div className="invalid-feedback">{errors.dateOfBirth}</div>}
        </div>
        <div className="mb-3">
          <label className="form-label">Gender</label>
          <select className={`form-select ${errors.gender ? 'is-invalid' : ''}`} name="gender" value={form.gender} onChange={handleChange}>
            <option value="">Select Gender</option>
            {genders.map(g => <option key={g} value={g}>{g}</option>)}
          </select>
          {errors.gender && <div className="invalid-feedback">{errors.gender}</div>}
        </div>
        <div className="mb-3">
          <label className="form-label">Email ID</label>
          <input type="email" className={`form-control ${errors.email ? 'is-invalid' : ''}`} name="email" value={form.email} onChange={handleChange} />
          {errors.email && <div className="invalid-feedback">{errors.email}</div>}
        </div>
        <div className="mb-3">
          <label className="form-label">H Number</label>
          <input type="text" className={`form-control ${errors.hNumber ? 'is-invalid' : ''}`} name="hNumber" value={form.hNumber} onChange={handleChange} maxLength={6} />
          {errors.hNumber && <div className="invalid-feedback">{errors.hNumber}</div>}
        </div>
        <div className="mb-3">
          <label className="form-label">Specialization</label>
          <select className={`form-select ${errors.specialization ? 'is-invalid' : ''}`} name="specialization" value={form.specialization} onChange={handleChange}>
            <option value="">Select Specialization</option>
            {specializations.map(s => <option key={s} value={s}>{s}</option>)}
          </select>
          {errors.specialization && <div className="invalid-feedback">{errors.specialization}</div>}
        </div>
        <div className="mb-3">
          <label className="form-label">Department</label>
          <input type="text" className={`form-control ${errors.department ? 'is-invalid' : ''}`} name="department" value={form.department} onChange={handleChange} />
          {errors.department && <div className="invalid-feedback">{errors.department}</div>}
        </div>
        <div className="mb-3">
          <label className="form-label">Designation</label>
          <select className={`form-select ${errors.designation ? 'is-invalid' : ''}`} name="designation" value={form.designation} onChange={handleChange}>
            <option value="">Select Designation</option>
            {designations.map(d => <option key={d} value={d}>{d}</option>)}
          </select>
          {errors.designation && <div className="invalid-feedback">{errors.designation}</div>}
        </div>
        <div className="mb-3">
          <label className="form-label">Work Experience (years)</label>
          <input type="number" className={`form-control ${errors.experience ? 'is-invalid' : ''}`} name="experience" value={form.experience} onChange={handleChange} min={0} />
          {errors.experience && <div className="invalid-feedback">{errors.experience}</div>}
        </div>
        <div className="mb-3">
          <label className="form-label">Password</label>
          <input type="password" className={`form-control ${errors.password ? 'is-invalid' : ''}`} name="password" value={form.password} onChange={handleChange} />
          {errors.password && <div className="invalid-feedback">{errors.password}</div>}
        </div>
        <div className="mb-3">
          <label className="form-label">Confirm Password</label>
          <input type="password" className={`form-control ${errors.confirmPassword ? 'is-invalid' : ''}`} name="confirmPassword" value={form.confirmPassword} onChange={handleChange} />
          {errors.confirmPassword && <div className="invalid-feedback">{errors.confirmPassword}</div>}
        </div>
        <button type="submit" className="btn btn-primary w-100" disabled={Object.keys(errors).length > 0}>Register</button>
        {txStatus && <div className="alert mt-3 mb-0 alert-info">{txStatus} {txHash && (<span>Tx: <a href={`https://sepolia.etherscan.io/tx/${txHash}`} target="_blank" rel="noopener noreferrer">{txHash.slice(0, 10)}...</a></span>)}</div>}
      </form>
    </div>
  );
};

export default DoctorRegistrationForm; 